body,
html {
  width: 100%;
  height: 100%;
  background-color: white;
}
#dynamic-root {
  position: relative;
  height: 100%;
}
#dynamic-root #dynamic-card {
  margin-top: 30px;
  background-color: white;
  height: 40%;
}
#dynamic-root #dynamic-card #new-dynamic-info {
  margin-left: 20px;
  margin-top: 20px;
  color: #000000;
  font-weight: bold;
  font-size: 1.3rem;
}
#dynamic-root #dynamic-card #dynamic-author {
  margin-left: 10px;
  color: black;
}
#dynamic-root #dynamic-card #dynamic-time {
  position: absolute;
  font-size: 0.7rem;
  color: #818181;
  margin-right: 10px;
  right: 10px;
}
#dynamic-root #dynamic-card #dynamic-content {
  margin-top: 15px;
  margin-left: 15px;
  font-size: 0.9rem;
  color: #666666;
}
#dynamic-root #dynamic-card #reply-count {
  display: flex;
  margin-top: 5px;
}
#dynamic-root #dynamic-card #reply-count .dynamic-reply-img {
  width: 20px;
  height: 20px;
  margin-top: 10px;
  margin-left: 15px;
}
#dynamic-root #dynamic-card #reply-count .dynamic-reply-text {
  font-size: 0.8rem;
  color: #818181;
  line-height: 20px;
  margin-left: 5px;
  margin-top: 9px;
}
#dynamic-root #dynamic-card #img-contaniner {
  display: flex;
  flex-direction: row;
}
#dynamic-root #dynamic-card #img-contaniner img {
  margin-left: 15px;
  margin-top: 15px;
  width: 120px;
  border-radius: 10px;
  height: 120px;
}
#dynamic-root #comment-item {
  padding: 20px;
  display: flex;
  flex-direction: column;
}
#dynamic-root #comment-item #item-line {
  background-color: #cccccc;
  height: 0.5px;
  margin-top: 10px;
}
#dynamic-root #comment-item #comment-pic {
  height: 80px;
  width: 80px;
  border-radius: 10px;
  margin-top: 10px;
}
#dynamic-root #comment-item #comment-index {
  display: flex;
  flex-direction: column;
}
#dynamic-root #comment-item #comment-index #commenter-time {
  margin-top: 6px;
  font-size: 0.6rem;
  color: #818181;
  margin-bottom: 5px;
}
#dynamic-root #comment-item #comment-content {
  font-size: 1rem;
  color: #818181;
}
#dynamic-root #round-btn {
  width: 60px;
  height: 60px;
  text-align: center;
  vertical-align: center;
  position: fixed;
  border-radius: 30px;
  background-color: #b1b107;
  bottom: 30px;
  right: 15px;
}
#dynamic-root #round-btn #new-pen {
  align-content: center;
  align-items: center;
  align-self: center;
  margin-top: 18px;
  margin-left: 2px;
  width: 25px;
  height: 25px;
}
#dynamic-root #root {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#dynamic-root #root #comment-input {
  position: relative;
  border-radius: 5px;
  width: 85%;
  padding: 2px;
  height: 80px;
  background-color: #e3cbf8;
}
#dynamic-root #root #comment-pic {
  height: 80px;
  width: 80px;
  border-radius: 10px;
  margin-top: 15px;
}
#dynamic-root #root #release-comment {
  margin-top: 20px;
  width: 95%;
  color: red;
  font-size: 0.9rem;
  height: 45px;
  text-align: center;
  line-height: 45px;
  background-color: #f6f6f6;
}

#lost-and-found-root-view {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
#lost-and-found-root-view #user-info {
  display: flex;
  flex-direction: row;
}
#lost-and-found-root-view #user-info #user-header-img {
  width: 50px;
  border-radius: 25px;
  margin: 20px;
  height: 50px;
}
#lost-and-found-root-view #user-info #user-name {
  display: flex;
  flex-direction: column;
}
#lost-and-found-root-view #user-info #user-name :first-child {
  font-size: 1rem;
  color: #000;
}
#lost-and-found-root-view #user-info #user-name :last-child {
  margin-top: 10px;
  font-size: 0.5rem;
  color: #999999;
}
#lost-and-found-root-view #type-lost {
  margin-left: 15px;
}
#lost-and-found-root-view #type-lost :first-child {
  color: #666666;
  font-weight: bolder;
  font-size: 1.4rem;
}
#lost-and-found-root-view #type-lost :last-child {
  color: #000;
  font-family: Arial;
  font-size: 1rem;
}
#lost-and-found-root-view #lost-content {
  margin-left: 15px;
  font-size: 0.8rem;
  color: #666666;
}
#lost-and-found-root-view .goods-url {
  width: 100%;
  margin-top: 10px;
}
#lost-and-found-root-view .img-tips {
  margin-left: 15px;
  font-size: 0.8rem;
  color: #4d4d4d;
}
#lost-and-found-root-view #bottom-btn {
  position: fixed;
  text-align: center;
  line-height: 50px;
  color: white;
  height: 50px;
  bottom: 0px;
}
#lost-and-found-root-view #bottom-btn :first-child {
  width: 60%;
  background-color: #4d4d4d;
}
#lost-and-found-root-view #bottom-btn :last-child {
  width: 40%;
  background-color: #009688;
}

#login-root {
  flex-direction: column;
  display: flex;
}
#login-root #top-view {
  width: 100%;
  flex: 4.5 1;
  background-color: #009688;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
}
#login-root #top-view #app-title {
  margin-top: 30px;
  color: white;
  font-size: 1.6rem;
  text-decoration: none;
  border-bottom: 2px solid red;
}
#login-root #top-view #app-info {
  margin-top: 40px;
  color: white;
  font-size: 0.8rem;
}
#login-root #bottom-view {
  width: 100%;
  flex: 5.5 1;
  background-color: #f6f6f6;
  flex-direction: column;
  display: flex;
  align-items: center;
}
#login-root #bottom-view #input-username {
  margin-top: 30px;
  width: 85%;
  height: 30px;
  background-color: #f6f6f6;
  border: 0px;
  outline: none;
  color: #009688;
  border-bottom: 2px solid red;
}
#login-root #bottom-view #input-password {
  margin-top: 20px;
  width: 85%;
  height: 30px;
  background-color: #f6f6f6;
  border: 0px;
  outline: none;
  color: #009688;
  border-bottom: 2px solid red;
}
#login-root #bottom-view #login-container {
  width: 85%;
  margin-top: 10px;
  display: flex;
  height: 100px;
  flex-direction: column;
  position: relative;
}
#login-root #bottom-view #login-container #btn-login {
  margin-top: 20px;
  height: 45px;
  width: 100%;
  color: white;
  text-align: center;
  line-height: 45px;
  font-size: 1rem;
  background-color: #009688;
}
#login-root #bottom-view #login-container #forget-password {
  position: absolute;
  bottom: 10px;
  margin-top: 10px;
  right: 0px;
  color: #009688;
  text-align: center;
  font-size: 0.7rem;
}
#login-root #bottom-view #team-info {
  position: absolute;
  bottom: 5px;
  align-self: center;
  font-size: 0.6rem;
  color: #7f7f7f;
}
input::-webkit-input-placeholder {
  color: #009688;
  font-size: 13px;
}


